import axios from "axios";
import { useEffect, useState } from "react";



const SelectMenu = ({setPosts,address,login})=>{

    const [topmenu,setTopmenu] = useState('좋아요');    

    const clickMenu=(e)=>{        
        
        if(e.target.nodeName =='I'){
            switch(e.target.className){
                case 'bi bi-bookmark-heart': setTopmenu('좋아요'); break;
                case 'bi bi-graph-up': setTopmenu('최고가'); break;
                case 'bi bi-graph-down': setTopmenu('최저가'); break;
            }
        }
    };

    useEffect(()=>{        
        axios.get('/marketapp/changePost.do',{params:{menu:topmenu,address:address}})
        .then(res=>{        
        setPosts(res.data);
        })      
    },[topmenu,login])

    return <>        
        <div className="row" style={{justifyContent:"end",marginRight:"10px",fontSize:"2rem"}} onClick={clickMenu}>
            <div><i className="bi bi-bookmark-heart" style={{marginLeft:"3px",color:"#ffc107",cursor:"pointer"}}></i></div>
            <div style={{marginLeft:"10px",marginRight:"10px"}}><i className="bi bi-graph-up" style={{marginLeft:"3px",color:"red",cursor:"pointer"}}></i></div>
            <div><i className="bi bi-graph-down" style={{marginLeft:"3px",color:"blue",cursor:"pointer"}}></i></div>
        </div>        
    </>
};

export default SelectMenu;