import axios from "axios";
import Caroucel from "./Caroucel";
import Card from "./Card";
import { useEffect,useState } from "react";
import SelectMenu from "./SelectMenu";
import "tippy.js/dist/tippy.css";
import "tippy.js/animations/scale.css";
import ReactWordcloud from "react-wordcloud";





function App() {

  //로그인 유무
  const [login,setLogin] = useState(false);
  //로그인 회원 정보
  const [user,setUser] = useState({});
  //게시글목록
  const [posts,setPosts] = useState([]);
  //워드클라우드
  const [words,setWords] = useState([]);

  const isLogin =()=>{
    axios.get('/marketapp/isLogin.do')
    .then(res=>{      
      if(login != res.data.login){
        setLogin(res.data.login);
        setUser(res.data.userinfo);
      }
    })

  };
  const getSearchWords=()=>{
    axios.get('/marketapp/getSearchWords.do')
    .then(res=>{            
      setWords(res.data);
    })
  };

  useEffect(()=>{
    getSearchWords();          
  },[]);

  useEffect(()=>{ 
  },[words]);
  
  const options = {
    colors: ["#1f77b4", "#ff7f0e", "#2ca02c", "#d62728", "#9467bd", "#8c564b"],
    enableTooltip: true,
    deterministic: false,
    fontFamily: "impact",
    fontSizes: [20, 60],
    fontStyle: "normal",
    fontWeight: "normal",
    padding: 5,
    rotations: 3,
    rotationAngles: [0, 90],
    scale: "sqrt",
    spiral: "archimedean",
    transitionDuration: 1000
  };


useEffect(()=>{
  isLogin();},[]);

useEffect(()=>{  
},[posts])


  
  return <>    
    <div className='app'>      
    <SelectMenu setPosts={setPosts} address={user.simpleaddress} login={login}/>    
    <Caroucel posts={posts}>
      {posts.map((post, i) => (
          <Card post={post} key={i}/>        
      ))}
    </Caroucel>
  </div>
  <div>      
      <div>
        <ReactWordcloud options={options} words={words} />
      </div>
    </div>
  </>
}

export default App;
