import './App.scss';


const Card=({post})=>{    
    const imagesrc= "../resources/assets/img/product_img/"+post.imagename;    
    const endday=post.endDate.toString().split(" ")[0];
    const baseprice = post.base_Price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
    const upperprice = post.upper_Price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');        

    return <>        
        <div className='card'>
            <p className='address'style={{fontFamily:"GmarketSans",fontWeight:"bold",color:"#1c1c1c"}}>{post.fulladdress}</p>
            <div className='row'>
                <img src={imagesrc} style={{width:"400px",height:"400px",borderRadius:"10px"}}/>
                <div className='col' style={{textAlign: "center",marginTop:"60px"}}>                                
                    <p style={{fontFamily:"GmarketSans",fontWeight:"bold",color:"#1c1c1c"}}>시작가 {baseprice}원</p>
                    <p style={{fontFamily:"GmarketSans",fontWeight:"bold",color:"#85adad"}}>최고가 {upperprice}원</p>
                    <p style={{fontFamily:"GmarketSans",fontWeight:"bold",color:"#1c1c1c"}}>좋아요 {post.likes}</p>
                    <p style={{fontFamily:"GmarketSans",fontWeight:"bold",color:"#1c1c1c"}}>마감일 {endday}</p>                                       
                </div>
            </div>
            <div style={{marginTop:"5px"}}>
                <h2 style={{fontFamily:"GmarketSans"}}>{post.title}</h2>
                <p style={{fontFamily:"GmarketSans",color:"#1c1c1c"}}>{post.content}</p>
            </div>
        </div>        
    </>

};

export default Card;